"use client";

import Image from "next/image";
import { useEffect } from "react";
import { useRouter } from "next/navigation";

import LogoEmVidros from "@/Logo EM Vidros.png";
import { Card, CardContent, CardHeader, CardTitle, CardDescription } from "@/components/ui/card";
import { LoginForm } from "@/components/auth/login-form";

export function LoginScreen() {
  const router = useRouter();

  // Enquanto a autenticação é mockada via localStorage, já evitamos
  // que um usuário autenticado veja novamente a tela de login.
  useEffect(() => {
    if (typeof window === "undefined") return;

    const isAuthenticated = window.localStorage.getItem("emhub_auth") === "true";
    if (isAuthenticated) {
      router.replace("/diretoria");
    }
  }, [router]);

  return (
    <div className="flex min-h-screen items-center justify-center bg-background px-4 py-8">
      <div className="mx-auto flex w-full max-w-5xl overflow-hidden rounded-[2rem] bg-card shadow-xl">
        {/* Coluna esquerda - hero inspirado na arte de referência */}
        <div className="relative hidden w-0 flex-1 items-stretch bg-gradient-to-br from-sky-950 via-sky-800 to-sky-600 md:flex md:w-1/2">
          <div className="pointer-events-none absolute inset-6 rounded-[2rem] border border-white/15" />
          <div className="relative z-10 flex h-full flex-col justify-between px-10 py-9 text-slate-50">
            <div className="text-xs font-medium tracking-[0.25em] text-slate-200/80">
              UMA FRASE GUIA
            </div>
            <div className="space-y-4 pb-10">
              <h1 className="max-w-xs text-4xl font-light leading-tight tracking-tight">
                Indicadores que{" "}
                <span className="font-semibold">clareiam a operação</span>.
              </h1>
              <p className="max-w-xs text-sm text-slate-100/80">
                Acompanhe, em um só lugar, o que importa para a gestão da EM Vidros:
                produção, comercial, estoques e eficiência.
              </p>
            </div>
          </div>
        </div>

        {/* Coluna direita - card de login */}
        <div className="flex w-full flex-1 items-center justify-center bg-background px-5 py-8 sm:px-8 md:w-1/2">
          <Card className="w-full max-w-sm border border-border/70 bg-card/95 shadow-md">
            <CardHeader className="space-y-6 pt-7">
              <div className="flex items-center justify-center">
                <div className="relative h-10 w-40">
                  <Image
                    src={LogoEmVidros}
                    alt="Logo EM Vidros"
                    fill
                    className="object-contain"
                    priority
                  />
                </div>
              </div>
              <div className="space-y-2 text-center">
                <CardTitle className="text-xl font-semibold tracking-tight">
                  Bem-vindo de volta
                </CardTitle>
                <CardDescription className="text-xs leading-relaxed text-muted-foreground">
                  Acesse o EM Hub para acompanhar os principais indicadores da operação
                  da EM Vidros.
                </CardDescription>
              </div>
            </CardHeader>
            <CardContent className="pb-7">
              <LoginForm />
            </CardContent>
          </Card>
        </div>
      </div>
    </div>
  );
}


