"use client";

import { useRouter } from "next/navigation";
import { useState } from "react";

import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";

const VALID_EMAIL = "admin@emvidros.com";
const VALID_PASSWORD = "admin123";

export function LoginForm() {
  const router = useRouter();
  const [email, setEmail] = useState(VALID_EMAIL);
  const [password, setPassword] = useState(VALID_PASSWORD);
  const [rememberMe, setRememberMe] = useState(false);
  const [error, setError] = useState<string | null>(null);
  const [isSubmitting, setIsSubmitting] = useState(false);

  function handleSubmit(event: React.FormEvent<HTMLFormElement>) {
    event.preventDefault();
    setError(null);
    setIsSubmitting(true);

    const isValid = email === VALID_EMAIL && password === VALID_PASSWORD;

    if (!isValid) {
      setError("Credenciais inválidas. Tente novamente.");
      setIsSubmitting(false);
      return;
    }

    if (typeof window !== "undefined") {
      // Implementação mockada, preparada para futura integração com better-auth.
      window.localStorage.setItem("emhub_auth", "true");

      if (rememberMe) {
        window.localStorage.setItem("emhub_auth_remember", "true");
      } else {
        window.localStorage.removeItem("emhub_auth_remember");
      }
    }

    // Após login, direciona para o painel operacional principal.
    router.push("/diretoria");
  }

  return (
    <form onSubmit={handleSubmit} className="space-y-4">
      <div className="space-y-2">
        <Label htmlFor="email">E-mail</Label>
        <Input
          id="email"
          type="email"
          autoComplete="email"
          value={email}
          onChange={(event) => setEmail(event.target.value)}
        />
      </div>
      <div className="space-y-2">
        <Label htmlFor="password">Senha</Label>
        <Input
          id="password"
          type="password"
          autoComplete="current-password"
          value={password}
          onChange={(event) => setPassword(event.target.value)}
        />
      </div>
      <div className="flex items-center justify-between gap-3 text-xs">
        <label className="inline-flex items-center gap-2 text-muted-foreground">
          <input
            id="remember-me"
            type="checkbox"
            className="h-3.5 w-3.5 rounded border-border bg-background text-primary focus-visible:outline-none focus-visible:ring-1 focus-visible:ring-ring"
            checked={rememberMe}
            onChange={(event) => setRememberMe(event.target.checked)}
          />
          <span>Lembrar de mim</span>
        </label>
        <button
          type="button"
          className="text-[11px] font-medium text-primary underline-offset-2 hover:underline"
        >
          Esqueci minha senha
        </button>
      </div>
      {error ? (
        <p className="text-xs font-medium text-destructive">{error}</p>
      ) : null}
      <Button type="submit" className="mt-1 w-full" disabled={isSubmitting}>
        {isSubmitting ? "Entrando..." : "Entrar"}
      </Button>
      <p className="pt-1 text-center text-[11px] leading-relaxed text-muted-foreground">
        Para testar, use <span className="font-medium">{VALID_EMAIL}</span> /{" "}
        <span className="font-medium">{VALID_PASSWORD}</span>.
      </p>
    </form>
  );
}


